* {
    margin: 0;
    padding: 0;
    box-sizing: border-box !important;
}

body {
    margin: 0;
    width: 100%;
    height: 100vh;
    font-family: "Exo", sans-serif;
    color: #fff;
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    animation: gradientBG 15s ease infinite;
}

@keyframes gradientBG {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}


/* Colors */


/* Background animation */

.wrapper {
    width: 100%;
    position: absolute;
    top: 25%;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.form-wrapper {
    width: 600px;
    padding: 40px 40px;
    border-radius: 1px;
    box-shadow: 0px 10px 50px #555;
    background-color: rgb(255, 132, 173);
    border-radius: 25px;
}

form {
    width: 100%;
}

a,
a:hover {
    text-decoration: none;
    color: #ddd;
    padding-top: 20px;
}

h1 {
    text-align: center;
    width: 100%;
    color: white;
    font-weight: lighter;
    font-family: 'Roboto', sans-serif;
    font-weight: 600;
}

h2 {
    text-align: left;
    width: 100%;
    color: white;
    font-weight: lighter;
    font-family: 'Roboto', sans-serif;
}

h3 {
    text-align: center;
    font-size: 16px;
    width: 100%;
    font-weight: lighter;
    font-family: 'Roboto', sans-serif;
    color: #eee;
    font-weight: 100;
}

.small-text {
    font-size: 12px;
    text-align: center;
}

label {
    font-size: 0.8em;
    margin-bottom: 0.25em;
    color: #222;
    font-weight: lighter;
    text-align: left;
}

input {
    margin: 5px;
    padding: 10cx 20px;
    border-radius: 5px;
    outline: none;
    border: 1px solid #cfcfcf;
}

input::placeholder {
    font-size: 1.2em;
    font-weight: lighter;
    color: #999;
}

input.error {
    border: 1px solid red;
}

.errorMessage {
    color: red;
    font-size: 0.7em;
    display: relative;
}

.login {
    /*this is a login area*/
    width: 100%;
}

.signin button {
    background-color: rgb(231, 93, 181);
    color: white;
    border: 0px solid black;
    border-radius: 5px;
    width: 100%;
    margin-top: 1em;
    padding: 8px 0px;
    font-size: 1em;
    font-weight: lighter;
    letter-spacing: 1px;
    margin-bottom: 0.25em;
}

.signin button:hover {
    color: white;
    background-color: rgb(233, 76, 175);
}

.signout button {
    background-color: rgb(231, 93, 181);
    color: white;
    border: 3px solid rgb(231, 93, 181);
    border-radius: 5px;
    width: 100%;
    margin-top: 5px;
    padding: 5px 15px;
    font-size: 14px;
    font-weight: 100;
    letter-spacing: 1px;
    margin-bottom: 0.25em;
}

.signout button:hover {
    color: white;
    background-color: rgb(233, 76, 175);
    border: 3px solid rgb(233, 76, 175);
}

.email small {
    color: black;
    font-weight: lighter;
}

.email small:hover {
    color: white;
    font-weight: lighter;
}

.page>p {
    margin-top: 25px;
    color: white;
    font-size: 12px;
}